<template>
  <div class="container d-flex flex-column min-vh-100 p-2" :class="{ 'pointers' : editing>=0 }" ref="mainContainer">
    <header class="d-flex justify-content-center bg-secondary bg-gradient text-white">{{title}}
      <button class="hidden btn btn-outline-light btn-sm mx-1" id="install-button" type="button">Install</button>
    </header>
    <div class="d-flex align-items-start w-100 h-100 justify-content-center sticky-top bg-light text-dark border"
      ref="todoForm">
      <form class="d-flex align-items-start flex-fill justify-content-center p-2" autocomplete="off"
        action="javascript:void(0);" @submit.prevent="addTodo">
        <label for="todo" class="form-label d-none">Todo</label>
        <input class="form-control rounded-0" v-model.trim="todo" @click.stop="scrollToTop" type="text" name="todo"
          id="todo" :placeholder="placeholder" :required="required" ref="addInput">
        <button class="btn btn-primary rounded-0" type="submit" name="button" ref="addButton">add</button>
      </form>
    </div>
    <div id="tools" v-if="todoArray.length" class="d-flex justify-content-between align-items-center text-muted order-1 bg-light border border-top-0 fs-5" :class="{locked: isLocked}">
        <span class="mx-2">total: {{todoArray.length}}</span>
        <span class="mx-2">left: {{counterTodo()}}</span>
        <span class="mx-2">sum: {{counterNumbers()}}</span>
        <div class="pointer me-2" @click="addLocalStorageItem('checkedSum', true)"><i title="Toggle count checked" :class="[checkedSum ?'toggle-checked gg-check-r':'gg-check-r']"></i></div>
        <div class="pointer me-2" onclick="window.print()"><i title="Print document" class="gg-printer"></i></div>
        <div class="pointer me-2" @click="exportLocalStorage()"><i title="Export localStorage" class="gg-usb"></i></div>
        <div class="pointer me-2" @click="importLocalStorage()"><i title="Import localStorage" class="gg-import"></i></div>
        <div class="pointer me-2" @click="addToggleClass('mainContainer', 'wide');addLocalStorageItem('wideScreen', true)"><i title="Toggle 700px wide" class="gg-screen pointer"></i></div>
        <div class="pointer me-2" @click="addLocalStorageItem('isLocked', true)"><i title="Toggle columns lock" :class="[isLocked ? 'gg-lock':'gg-lock-unlock']"></i></div>
    </div>
    <div class="custom-table fs-5">
      <div v-if="todoArray.length" class="fs-6 bg-light text-muted">No.</div>
      <div v-if="todoArray.length" class="fs-6 bg-light pointer"
        @click="sortBy('todo', this.sortedItems[0], (a) =>  a.toUpperCase())">{{this.sortedItems[0]?'&darr;':'&uarr;'}}</div>
      <div v-if="todoArray.length" class="fs-6 bg-light pointer" @click="sortBy('status', this.sortedItems[1])">{{this.sortedItems[1]?'&darr;':'&uarr;'}}</div>
      <div v-if="todoArray.length" class="fs-6 bg-light"></div>
      <div v-if="todoArray.length" class="fs-6 bg-light"></div>
      <div v-if="todoArray.length" class="fs-6 bg-light"></div>
      <list-item v-for="(todo, index) in todoArray" :key="index" @local="addToLocalStorage" :todoL="todoLength()"
        @remove="removeTodo" @edit="editTodo" :item="todo" @priority="setImportance" @info="infoTodo" :index="index">
      </list-item>
    </div>
    <div class="text-center m-auto" v-if="!todoArray.length">All done, good job!</div>
  </div>
  <div id="info" class="container-fluid hidden text-right bg-light" ref="info">
    <button type="button" @click="$event.currentTarget.parentElement.classList.add('hidden')" class="btn-close p-2" aria-label="Close"></button>
    <div class="p-2 border">
      <div class="mb-2 border p-2 bg-white">
        <div class="fw-bold pe-2">Todo:</div>
        <div>{{itemInfo.todo}}<span class="text-danger pointer" @click="removeElem(itemInfo.index,0, 1);$event.currentTarget.parentElement.parentElement.parentElement.parentElement.classList.add('hidden')"><i class="gg-trash"></i></span>
        </div>
      </div>
      <div class="mb-2 border p-2 bg-white" v-if="typeof itemInfo.timeAdd !=='undefined' && !!itemInfo.timeAdd.length">
        <div class="fw-bold pe-2">Time added:</div>
        <div>
          <div v-for="(oneItem, index) in itemInfo.timeAdd" :key="index">{{oneItem}}<span class="text-danger pointer"
              @click.stop="removeElem(itemInfo.index,0,'timeAdd')"><i class="gg-trash"></i></span></div>
        </div>
      </div>
      <div class="mb-2 border p-2 bg-white" v-if="typeof itemInfo.timeFin !=='undefined' && itemInfo.timeFin.length">
        <div class="fw-bold pe-2">Time finished:</div>
        <div>
          <div v-for="(oneItem, index) in itemInfo.timeFin" :key="index">{{oneItem}}<span class="text-danger pointer"
              @click.stop="removeElem(itemInfo.index,index,'timeFin')"><i class="gg-trash"></i></span></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import listItem from './components/listItem'

  export default {
    name: 'App',
    data() {
      return {
        editing: -1, //hope that item index never be less then 0
        flag: false,
        importance: ['z', 'mid', 'hight'],
        lang: 'lt-LT',
        placeholder: 'Type here..',
        required: false,
        title: 'Todo-app',
        todo: '',
        isLocked: false,
        checkedSum: true,
        status: 'z', // z just for sorting (hack ;/)
        itemInfo: [],
        timeAdd: [],
        timeFin: [],
        sortedItems: [false,false],
        columnsToShow: [true, true, true, true, true, true],
        todoArray: [],
        todoLength: () => this.todoArray.length.toString().length
      }
    },
    components: {
      listItem
    },
    mounted() {
      // this todoArray all array from localStorage
      this.todoArray = this.parser() || []
      this.isLocked = this.getLocalStorageItem('isLocked') || false
      this.checkedSum = this.getLocalStorageItem('checkedSum') || false
      this.getLocalStorageItem('wideScreen') && this.$refs.mainContainer.classList.add('wide')
      // if array empty focus todo input
      if (!this.todoArray.length) this.flag = true
       
      const [first, second] = this.getLocalStorageItem('sortedItems') || []
      this.sortedItems[0] = !!first
      this.sortedItems[1] = !!second
        // mount todo app install PWA
      let externalScript = document.createElement('script')
      externalScript.setAttribute('src', 'js/app-install.min.js')
      document.head.appendChild(externalScript)
    },
    watch: {

      flag() {
        if (this.flag) {
          window.setTimeout(() => {
            this.$refs.addInput.focus()
          }, 200)
        }
        this.flag = false
      }
    },
    methods: {
      getLocalStorageItem(key) {
        if (typeof (Storage) !== 'undefined')
          return JSON.parse(window.localStorage.getItem(key))
      },
      addLocalStorageItem(key, value) {
        this[key] = !this[key]
        if (typeof (Storage) !== 'undefined')
          //just remove if false
          this[key] !== this.getLocalStorageItem(key) ? window.localStorage.setItem(key, value) : window.localStorage.removeItem(key)
      },
      saveData(data, fileName){
          const a = document.createElement('a')
          const json = JSON.stringify(data, null, 2)
          const blob = new window.Blob([json], {type: 'octet/stream'})
          const url = window.URL.createObjectURL(blob)
          a.href = url
          a.download = fileName
          a.click()
          window.URL.revokeObjectURL(url)
      },
      exportLocalStorage(){
        const data = {}
        for (let i = 0; i < window.localStorage.length; i++) {
          data[window.localStorage.key(i)] = this.getLocalStorageItem(window.localStorage.key(i))
        }
        this.saveData(data, 'todo-list-' + this.setTime().split(/[: ]/).join("-") + '.json')
      },
      importLocalStorage() {
        let input = document.createElement('input')
        input.type = 'file'
        const that = this
        input.onchange = function (e) {
          const file = e.target.files[0]
          if (!file) return
          const reader = new window.FileReader()
          reader.onload = function (e) {
            that.addLocalStorageItem('checkedSum', JSON.parse(e.target.result).checkedSum)
            that.addLocalStorageItem('isLocked', JSON.parse(e.target.result).isLocked)
            that.addLocalStorageItem('todoApp', JSON.stringify(JSON.parse(e.target.result).todoApp))
            window.location.reload()
            //this.todoArray = JSON.parse(e.target.result).todoApp
          }
          reader.readAsText(file)
        }
        input.click()
      },
      sortBy(field, reverse, primer) {
        this.todoArray.sort(this.sortList(field, reverse, primer))
        if(field === 'todo') {
          this.sortedItems[0] = !this.sortedItems[0]
          // this.sortedItems[1] = !this.sortedItems[0]
        }
        else if(field === 'status') {
          this.sortedItems[1] = !this.sortedItems[1]
          // this.sortedItems[0] = !this.sortedItems[1]
        }
        window.localStorage.setItem('sortedItems', JSON.stringify(this.sortedItems))
        
        this.addToLocalStorage()
      },
      sortList(field, reverse, primer) {
        const key = primer ? x => primer(x[field]) : x => x[field]
        reverse = !reverse ? 1 : -1
        return (a, b) => (a = key(a), b = key(b), reverse * ((a > b) - (b > a)))
      },
      removeElem(item, index, e) {
        if (e === 1) this.todoArray.splice(item, 1)
        else if (e === "timeAdd") this.todoArray[item][e].splice(index, 1)
        else this.todoArray[item][e].splice(index, 1)
        this.addToLocalStorage()
      },
      infoTodo(index) {
        this.$refs.info.classList.remove('hidden')
        this.itemInfo = {
          index,
          ...this.todoArray[index]
        }
      },
      addToggleClass(element, nameOfClass){
        this.$refs[element].classList.toggle(nameOfClass)
      },
      parser() {
        try {
          return this.getLocalStorageItem('todoApp')
        } catch (e) {
          // remove all wrong values and reload just in case to not break app
          window.localStorage.removeItem('todoApp')
          window.location.reload()
        }
      },
      counterTodo() {
        return this.todoArray.filter(todo => !todo.done).length
      },
      addToLocalStorage(index, e) {
        // if array exist (not deleted) set done at first
        if (this.todoArray[index] && e) {
          this.todoArray[index].done = e.target.checked
          // if done and minute passed time!==time (prevent from random clicking and writing time)
          // because we cant finish something in less than minute :)
          if (typeof this.todoArray[index].timeFin === "undefined") this.todoArray[index].timeFin = []
          if (this.todoArray[index].done && this.todoArray[index].timeFin[this.todoArray[index].timeFin.length - 1] !=
            this.setTime()) {
            this.todoArray[index].timeFin.push(this.setTime())
          }
          // else delete this.todoArray[index].timeFin
        }
        // flash message in title for 800ms that it saved in local storage
        document.title = 'TODO â€“ SAVED ðŸ’¾'
        // set title back to Todo
        let tim = setTimeout(() => {
          document.title = 'TODO'
          clearTimeout(tim)
        }, 2000)
        // rewrite all local storage
        typeof (Storage) !== 'undefined' && window.localStorage.setItem('todoApp', JSON.stringify(this.todoArray))
      },
      addTodo() {
        if (this.todo.trim().length === 0) {
          this.placeholder = 'Empty field!'
          // focus after 99ms because otherwise not work
          setTimeout(() => this.$refs.addInput.focus(), 99)
          // removed showing popup required required required...
          // setTimeout(()=>this.required = true, 100)
          return
        }

        const values = [].concat.apply([], this.todoArray.filter(e => isNaN(e.todo)).map(e => e.todo.toUpperCase()))

        if (this.editing >= 0) {
          this.todoArray[this.editing].todo = this.todo
          this.$refs.addButton.innerHTML = 'add'
          document.body.style.overflow = 'visible'
          this.$refs.todoForm.classList.add('sticky-top')
          this.$refs.todoForm.classList.remove('transforms')
          this.editing = -1
        } else {
          this.flag = true

          if (values.indexOf(this.todo.toUpperCase().trim()) > -1) {
            this.todo = ''
            this.placeholder = 'Value already exists!'
            return
          }
          const string = this.todo.replace(/,/g, '.').trim().replace(/\s+/g, ' ').trim()
          this.todoArray.unshift({
            todo: string.charAt(0).toUpperCase() + string.slice(1),
            done: false,
            status: this.status, //set last status
            timeAdd: [this.setTime()],
            timeFin: []
          })
        }
        this.todo = ''
        this.placeholder = 'Type here..'
        this.$refs.addInput.focus()
        this.scrollToTop()
        this.addToLocalStorage()
      },
      removeTodo(index) {
        this.todo = ''
        this.editing = -1
        this.todoArray.splice(index, 1)
        this.addToLocalStorage()
      },
      editTodo(index) {
        this.todo = this.todoArray[index].todo
        this.editing = index
        this.$refs.addButton.innerHTML = 'save'
        document.body.style.overflow = 'hidden'
        this.$refs.todoForm.classList.add('transforms')
        this.$refs.todoForm.classList.remove('sticky-top')
        this.$refs.addInput.focus()
      },
      scrollToTop() {
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        })
        // set placeholder input on click
        this.placeholder = 'Type here..'
      },
      setImportance(index) {
        let priority = this.importance.indexOf(this.todoArray[index].status)
        if (++priority > 2) priority = 0
        this.todoArray[index].status = this.status = this.importance[priority]
        this.addToLocalStorage()
      },
      setTime() {
        const options = {
          hour: '2-digit',
          minute: '2-digit',
          hour12: false
        }
        return (new Date()).toLocaleDateString(this.lang, options)
      },
      counterNumbers() {
        const numArray = []
        // push only numbers to array
        this.todoArray.forEach(e => {
          if (typeof e.todo !== 'undefined' && (this.checkedSum ? e.done : !e.done) && !isNaN(parseFloat(e.todo.match(
              /[+-]?((?=\.?\d)\d*\.?\d*)/g)))) {
            numArray.push(parseFloat(e.todo.match(/[+-]?((?=\.?\d)\d*\.?\d*)/g)))
          }
        })
        // return sum of numbers
        return numArray.reduce((a, b) => {
          const n1 = a.toString().split('.')[1]
          const n2 = b.toString().split('.')[1]
          const len1 = (n1 && n1.length) || 0
          const len2 = (n2 && n2.length) || 0
          return parseFloat((a + b).toFixed(len1 + len2))
        }, 0)
      }
    }
  }
</script>
